<template>
  <v-navigation-drawer v-model="drawer" floating clipped app>
    <v-list class="py-0">
      <v-list-item
        active-class="active"
        style="height: 75px; font-size: 16px; font-weight: 600"
        class="pl-12"
        :to="item.to"
        v-for="item in items"
        :key="item.title"
        link
      >
        <!-- <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon> -->

        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  name: "TheSidebar",
  props: {
    isSidebarOpen: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      drawer: this.isSidebarOpen,
      items: [
        { title: "Dashboard", to: "/", icon: "mdi-view-dashboard" },
        { title: "Staff", to: "/staff", icon: "mdi-account-supervisor" },
        // { title: "About", to: '/about', icon: "mdi-forum" },
      ],
    };
  },
  watch: {
    drawer(state) {
      this.$emit("update:isSidebarOpen", state);
    },
    isSidebarOpen(state) {
      this.drawer = state;
    },
  },
};
</script>

<style lang="scss" scoped>
.active {
  background-color: #6433d7;
  color: white !important;
}
.v-list-item--link::before {
  background: unset;
}
</style>